body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td { margin:0; padding:0; } 
body, button, input, select, textarea { font:12px/1.5tahoma, arial, \5b8b\4f53; } 
h1, h2, h3, h4, h5, h6{ font-size:100%; } 
address, cite, dfn, em, var { font-style:normal; } 
code, kbd, pre, samp { font-family:couriernew, courier, monospace; } 
small{ font-size:12px; } 
ul, ol { list-style:none; } 
a { text-decoration:none; } 
a:hover { text-decoration:underline; } 
sup { vertical-align:text-top; } 
sub{ vertical-align:text-bottom; } 
legend { color:#000; } 
fieldset, img { border:0; } 
button, input, select, textarea { font-size:100%; } 
table { border-collapse:collapse; border-spacing:0; }
.main-title{
    margin:50px 0;
    font-size:48px;
    text-align:center;
}

.spacer{
    display:block;
    margin:100px auto;
    width:80%;
    border:2px solid transparent;
    border-image:linear-gradient(to left,transparent,#ccc,transparent);
    border-image-slice:1;
}
.table-wp{
    /* 如果超出400px height 将会显示scroll */
    margin:auto;
    padding:20px;
    max-width:785px;
    max-height:400px;
    border:1px solid #eee;
    border-radius: 4px;
    font-size:14px;
    color:#555;
    overflow:auto;
}

.table-wp table{
    /* 如果屏幕 w < 450 显示scroll */
    width:100%;
    min-width:450px;
}

.table-wp thead{
    color:#777;
}

.table-wp th,td{
    padding:8px 10px;
    text-align:left;
    border:1px solid #ddd;  
    
}
.table-wp tbody tr:nth-child(even){
    background-color:#FAFAFA;
}
.table-wp tbody tr:hover{
    background-color:rgba(204, 221, 255, 0.137);
    
}
.dropdown-wp{
    width:500px;
    padding:20px;
    border:1px solid #ccc;
    margin:auto;
}
/* 最外层的下拉菜单输入框 */
.com-box{
    position:relative;
    box-sizing: border-box;
    margin:auto;
    width:400px;
    height:34px;
    padding-top:4px;
    border:1px solid #ccc;
    border-radius: 4px;
}

/* 已将添加的标签 */
.chip{
    position:relative;
    float:left;
    line-height:24px;
    padding:0 24px 0 6px;
    margin-left:4px;
    background-color:rgb(61, 42, 233); 
    border-radius: 12px 12px;
    color:white;
    font-size:14px;
}
    .chip .delete{
        position:absolute;
        right:5px;
        top:20%;
    }


/* 添加新标签，打开下拉下单的按钮 */
.add-btn{
    position:absolute;
    right:5px;
    top:50%;
    margin-top:-10px;
    width:18px;
    height:18px;
    border:1px solid #ccc;
    border-radius:50%;  
}
    .add-btn:hover{
        border-color:rgb(163, 214, 255); 
    }
    .add-btn:before,.add-btn:after{
        position:absolute;
        left:0;
        top:0;
        bottom:0;
        right:0;
        margin:auto;
        content:"";
        display:block;
        height:2px;
        width:80%;
        background-color:#ccc;
        border-radius:1px;
        transition:.2s;
    }
    .add-btn:hover:before, .add-btn:hover:after{
        background-color:rgb(163, 221, 255); 
    }

    .add-btn:before{
        -webkit-transform:rotate(90deg);
                transform:rotate(90deg);
    }
    /* opened 旋转icon*/
    .add-btn.open:before{
        -webkit-transform:rotate(45deg);
                transform:rotate(45deg);
    }
    .add-btn.open:after{
        -webkit-transform:rotate(-45deg);
                transform:rotate(-45deg);
    }

/* dropdown 菜单 */
.dropdown-menu{
    overflow:hidden;
    box-sizing:border-box;
    margin:auto;
    width:400px;
    height:auto;
    box-shadow: 0px 4px 5px 2px rgba(0,0,0,0.1);
    /* border:1px solid #ccc; */
    border-radius: 4px;
    
    font-size:14px;
    color:#888;
    transition:.2s;
}


    /* searchBar */
    .dropdown-menu .search-bar-wp{
        position:relative;
        overflow:hidden;
        height:40px;
        background-color:#eee;

    }
    .dropdown-menu .search-bar{
        display:block;
        box-sizing: border-box;
        margin:5px auto;
        padding:0 30px;
        width:80%;
        line-height:28px;
        min-height:30px;
        border:1px solid #ccc;
        border-radius: 14px;
        outline: none;
        /* font-weight: 900; */
    }
    .dropdown-menu .search-bar:focus{
        border:1px solid rgb(157, 146, 255);
    }
    .dropdown-menu .icon-search{
        position:absolute;
        top:11px;
        left:48px;
        color:#666;
        font-weight: normal;
    }

    /* option list */
    .dropdown-menu .checkbox-list .input-wp{
        margin:20px 0;
        padding-left:30px;
        color:#444;
        
    }

    .dropdown-menu .checkbox-list input{
        display:inline-block;
        margin-right:20px;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
        -ms-appearance:none;
        outline:none;
        vertical-align:middle;
    }
    .dropdown-menu .checkbox-list input::before{
        content:" ";
        display:inline-block;
        width:18px;
        height:18px;
        border-radius:6px;
        background:url(data:image/png;base64,) no-repeat 0 0/18px 36px;
        cursor:pointer;
    } 

    .dropdown-menu .checkbox-list input:checked::before{
        content:" ";
        background:url(data:image/png;base64,) no-repeat 0 -18px/18px 36px;
    }

    .dropdown-menu .checkbox-list label{
        cursor:pointer;
    }




.cross{
    position:relative;
    display:block;
    margin-top:-1px;
    width:14px;
    height:14px;
    cursor: pointer;
}
.cross:before,.cross:after{
    position:absolute;
    content:"";
    top:50%;
    display:block;
    height:2px;
    width:100%;
    background-color:white;
    border-radius:1px;
    /* transform-origin:center; */
}
.cross:hover:before,.cross:hover:after{
    background-color:rgba(255, 255, 255, 0.822);
    /* transform-origin:center; */
}

.cross:before{
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
}

.cross:after{
    -webkit-transform:rotate(-45deg);
            transform:rotate(-45deg);
}

